<template>
  <div class="page">
    <mt-header  title="åå²æ¶æ¬¾">
      <mt-button slot="left" @click="$router.back()" icon="back">è¿å</mt-button>
    </mt-header>
    <div class="page_bd">
      <div class="green_top">
        <div class="green_bl text-center">
          <div   ><button class="date_btn " @click="dateClick">
            <span class="padding-right5 ">{{yesterday}}</span>
            <span class="direct_bottom main_color"> > </span></button></div>
          <div v-show="!noData">
            <p class="big_num">{{cashObj.totalMoney | formatMoney}} <small>å</small></p>
            <p class="dark_99">å±{{cashObj.totalNumber}}ç¬</p>
          </div>
          <div v-if="noData" class="padding-top15" style="height:40vw;">
            <img src="../../assets/img/publicPage/no-data.png" style="height: 100%;">
          </div>
        </div>
      </div>
      <div v-show="!noData" class="weui-cells " style="margin-top:0;    padding-top: 5vw;" >
        <div v-for="(item,index) in list" :key="index" class="weui-cell">
          <div class="weui-cell__bd">
            <p>{{item.cstName}}</p>
            <p style="font-size: 13px;color: #888888;">{{ item.fillDate | today }}</p>
          </div>
          <div class="weui-cell_ft">
            {{ item.moneyTot  | setPrice}}
          </div>
        </div>
      </div>
    </div>
    <datetime-picker ref="picker" type="date" v-model="date"
                     @confirm="dateConfirm"
                     year-format="{value} å¹´"
                     month-format="{value} æ"
                     date-format="{value} æ¥"></datetime-picker>
  </div>
</template>
<script>
import {mapGetters} from 'Vuex'
import { DatetimePicker } from 'mint-ui';
export default {
  name: 'cashHistory',
  data () {
    return {
      date: '',
      yesterday: '',
      noData: true,
      cashObj: {
        fillDate: Date.now(),
        totalMoney: 0,
        totalNumber: 0
      },
      list: []
    }
  },
  created () {
    this.getPageData()
    this.date = this.getYesterday()
    this.yesterday = this.getYesterday()
  },
  components: {
    'DatetimePicker': DatetimePicker
  },
  computed: {
    ...mapGetters({
      'user': 'user'
    })
  },
  methods: {
    async getPageData () {
      let url = '/ets/payment/fnpaid/getHistoryPay'
      let res = await this.$http.post(url, {
        orgID: '11091315263400010000',
        fillpro: '20',
        fillDate: this.yesterday
        // 'orgID': this.user.OrgID, // orgId
        // 'fillpro': this.user.memberId, // åå·¥id
        // 'fillDate': '2018-11-13' // æ¥æ
      })
      let data = res.data
      if (data) {
        this.list = data.customerData
        this.cashObj = {
          fillDate: data.fillDate,
          totalMoney: data.totalMoney,
          totalNumber: data.totalNumber
        }
        this.noData = false
      } else {
        this.noData = true
      }
    },
    dateClick () {
      this.$refs.picker.open()
    },
    dateConfirm (date) {
      console.log(date)
      this.yesterday = this.formatDate(date.valueOf())
      this.getPageData()
    },
    getYesterday () {
      let date = new Date()
      date.setDate(date.getDate() - 1)
      return this.formatDate(date.valueOf())
    },
    formatDate (time) {
      time = new Date(parseInt(time))
      var year = time.getFullYear()
      var month = time.getMonth() + 1
      var day = time.getDate()
      month = month < 10 ? '0' + month : month
      day = day < 10 ? '0' + day : day
      time = year + '-' + month + '-' + day
      return time
    }
  }
}
</script>

<style lang="scss" scoped>
  .green_top{
    background:#0CC88D;
    text-align:center;
    padding: 20px 15px;
    height: 30vw;
    & .green_bl {
      min-height: 100px;
      background: #FFF;
      border-radius: 10px;
      box-shadow: 0px 0px 6px rgba(51, 51, 51, 0.48);
      z-index: 4;
      position: relative;
      padding:10px;
      .big_num {
        font-size:24px;
        font-weight: bold;
      }
      .weui-cell_access {
        -webkit-tap-highlight-color: #FFF;
      }
      .date_btn {
        outline:none;
        border:none;
        background: transparent;
        color: #131313;
      }
      .direct_bottom{
        transform: rotateZ(90deg);
        display: inline-block;
        font-size: 18px;
        line-height: 20px;
        vertical-align: middle;
      }
    }
  }
</style>



